body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.content-block {
    text-align: center;
    padding: 40px 0;
}

.content-block h1 {
    text-transform: uppercase;
    font-weight: bold;
    color: #84c54e;
}
.max-height {
    max-height: 350px;
}

.carousel-item:after {
    content:"";
    display:block;
    position:absolute;
    top:0;
    bottom:0;
    left:0;
    right:0;
    background:rgba(0,0,0,0.3);
}

.car-1 {
    font-size: 2rem;
}

.carousel-caption {
    font-size: 1.2em;
}


.list-click {
    background: rgb(185, 185, 241);
    padding: 10px;
    border: 3px solid #fff;
}
.btn {
    margin-bottom: 5px;
    text-align: left;
    font-size: .9em;
    border: 1px solid #ccc;
}

.btn:hover {
    color: #84c54e;
}
.jumbotron {
    background-image: url(/static/media/planner.e9f4e58a.jpg);
    background-size: cover;
    color: #fff;
    max-height: 180px;
    margin-top: 20px;
}

.jumbotron a, .jumbotron a:visited {
    color: #fff;
}
.delete-btn {
  float: right;
  color: #e74944;
}

.list-overflow-container {
  max-height: 545px;
  border: 1px #DDDDDD solid;
  border-radius: 4px;
  overflow-y: scroll;
  overflow-x: hidden;
}

.list-overflow-container ul {
  margin: 0;
}

.list-overflow-container li {
  border-radius: 0px;
  border-left: none;
  border-right: none;
}

.list-overflow-container li * {
  cursor: pointer;
}

.list-overflow-container li a {
  color: #2A878C;
}

.list-overflow-container li:first-child {
  border-top: none;
}

.list-overflow-container li:last-child {
  border-bottom: none;
}

.nav-link, .nav-link:visited {
    background: #fff;
    margin-left: 5px;
    font-size:.9em;
    border-radius: 15px;
    padding: 0 30px;
}

.nav-link:hover {
    background: #84c54e;
    color: #fff !important;
}
