.max-height {
    max-height: 350px;
}

.carousel-item:after {
    content:"";
    display:block;
    position:absolute;
    top:0;
    bottom:0;
    left:0;
    right:0;
    background:rgba(0,0,0,0.3);
}

.car-1 {
    font-size: 2rem;
}

.carousel-caption {
    font-size: 1.2em;
}